.carousel {
  /* the slides */
  background-color: #f0eeee;
  padding: 25px 0 25px;
  /* the parent */
}
.carousel:has(.slick-dots) {
  padding-bottom: 0;
}
.carousel .slick-slide {
  margin: 0 calc(25px / 2);
}
.carousel .slick-list {
  margin: 0 -25px;
}
.carousel .slick-dots {
  position: relative;
  margin: 1rem 0 1rem 0;
  display: flex;
  justify-content: center;
  bottom: 0;
  margin: 0;
  padding: 1rem 0;
  list-style-type: none;
}
.carousel .slick-dots li {
  margin: 0 0.25rem;
}
.carousel .slick-dots li button {
  border: 1px solid #323232;
}
.carousel .slick-dots button {
  display: block;
  width: 1rem;
  height: 1rem;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: #f1f1fe;
  text-indent: -9999px;
}
.carousel .slick-dots li.slick-active button {
  background-color: #323232;
}
.carousel__item {
  background-color: #fff;
  border-radius: 0 0 45px 0;
  display: flex !important;
  flex-direction: column;
  height: 780px;
  justify-content: space-between;
  position: relative;
  width: 100%;
}
.carousel__item:active {
  cursor: grab;
}
.carousel__item-bottom {
  padding: 24px;
}
.carousel__image {
  height: 100%;
  max-height: 200px;
  left: 0;
  object-fit: cover;
  top: 0;
  width: 100%;
  z-index: 1;
}
.carousel__inner {
  max-width: 1324px;
  padding: 0 calc(25px * 2);
  margin: 0 auto;
}
.carousel__content {
  position: relative;
  padding: 20px 45px;
  z-index: 2;
}
@media screen and (max-width: 728px) {
  .carousel__content {
    overflow: scroll;
    max-height: 410px;
  }
}
.carousel__cta {
  background-color: #323232;
  color: #fff;
  font-weight: 600;
  border-radius: 0 0 25px 0;
  padding: 12px 24px;
  display: block;
  text-decoration: none;
  transition: 0.15s ease-in all;
}
.carousel__cta:hover {
  background-color: #191919;
}

.wysiwyg h2 {
  margin: 0 0 1rem;
  font-size: 2.4rem;
}
.wysiwyg ul,
.wysiwyg ol {
  padding: 0;
  margin-left: 17px;
}
.wysiwyg li,
.wysiwyg p {
  font-size: 1rem;
  line-height: 1.6rem;
}